.minimg {
    width: 130px;
    height: 130px;
    display: flex;
    gap: 20px;
    padding-top: 10px;
    padding-bottom: 20px;
    position: relative;
    /*   position: relative  */
}


.minimg>button {
    position: absolute;
    /*  position: absolute  */
    top: 50%;
    /*       */
    transform: translateY(-50%);
    /*     */
    margin-right: -65px;
    /*       */
    z-index: 2;

}

.minimg .nextimg>button {
    position: absolute;
    /*  position: absolute  */
    top: 50%;
    /*       */
    transform: translateY(-50%);
    /*     */
    margin-left: -30px;
    z-index: 2;
    width: 25px;
}

.minimg1 {
    width: 130px;
    height: 130px;
}

.previousnext {
    border-radius: 50%;
    background-color: rgb(156, 153, 153);
    width: 25px;
    height: 25px;
    cursor: pointer;
    border: none;
    margin-left: -17px;
    opacity: 0.4;
}

.titletype {
    font-weight: bold;
    font-size: 15px;
}

.recommen {
    padding-top: 30px;
}